@import url("https://fonts.googleapis.com/css2?family=Noto+Serif+KR:wght@500&display=swap");

* {
  font-family: "Noto Serif KR", serif;
}
:root {
  --main-width: 516px;
  --small-width: 320px;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 5vh;
  width: 100vw;
}

section h1 {
  text-align: center;
  margin: 5vh;
}

.invitation {
  max-width: var(--main-width);
}
.wedding-hall {
  max-width: var(--main-width);
  word-break: keep-all;
}
.wedding-hall-detail {
  font-size: 13px;
  word-break: keep-all;
}
.Layout {
  display: flex;
  justify-content: center;
  justify-items: center;
  align-items: center;
  align-content: center;
  flex-direction: column;
}

@media (max-width: 516px) {
  .MuiMasonry-root img {
    text-align: center;
    min-width: 100vw;
    overflow-x: clip;
  }
  .comment-card {
    width: var(--small-width);
  }
  .comment-stack {
    width: var(--small-width);
  }
  .comment-form {
    width: var(--small-width);
  }
  .footer {
    width: var(--small-width);
  }
  .google-map {
    width: var(--small-width);
  }
  .invitation {
    max-width: var(--small-width);
  }
  .wedding-hall {
    max-width: var(--small-width);
    word-break: keep-all;
  }
  .maeum-sentence {
    width: var(--small-width);
  }
  .google-map {
    width: var(--small-width);
    margin-top: 5vh;
  }
}
.google-map {
  max-width: var(--main-width);
  margin-top: 5vh;
}
.map-icon {
  max-width: 50px;
  margin: 0 10%;
}
.intro-container {
  position: relative;
  overflow-x: hidden;
  width: 100%;
  text-align: center;
}
.intro-image {
  height: 100vh;
  margin: 0 -100%;
}
.intro-wish {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* color: rgba(0, 0, 0); */
  color: black;
  font-size: 45px;
  /* font-weight: 700; */
  word-break: keep-all;
}
.intro-names {
  position: absolute;
  left: 60%;
  top: 20.5%;
  color: black;
  font-size: 30px;
}
.intro-hall {
  font-size: 20px;
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  text-align: center;
}
.intro-hall p {
  word-break: keep-all;
}

.map-groups {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 3vh;
}

.map-image {
  max-width: var(--main-width);
}

.maeum-sentence {
  max-width: var(--main-width);
}

.comment-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.comment-name {
  font-weight: bold;
  color: black;
}
.comment-date {
  color: gray;
  font-size: 12px;
  text-align: right;
}
.comment-text {
  font-style: italic;
  text-align: center;
}
.comment-create-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.comment-form {
  min-width: 320px;
  max-width: 536px;
  width: 100%;
}
.footer {
  min-width: 320px;
  max-width: 536px;
  width: 100%;
}
